/* CreditPage.css */
body {
    background-color: #121212; /* Dark gray color */
    margin: 0; /* Remove default body margin */
    padding: 0; /* Remove default body padding */
    color: #fff; /* Set default text color to white */
  }
  

/* Container styling */
.credit-page-container {
    justify-content:center;
    max-width: 400px;
    
    margin: 0 auto;
    margin-top: 75px;
    padding: 20px;
    border-radius: 15px;
    background-color: #2c2e33; /* Dark gray background */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3); /* Shadow */
    /* border: 2px solid #ccc; */
    color: #202124; /* White text */
    align-items: center;
    text-align:center;
  }

  .Title{
    text-align:center;
    

  }

  .credit-card-details {

    color: #202124; /* White text */
  }

  /* CSS */

/* Define colors for different data */
/* CSS */

.credit-card-details p {
  color: #fff; /* Set label color to white */
  margin-bottom: 10px;
}

.credit-card-details p strong {
  font-weight: bold; /* Make label text bold */
}

.credit-card-details p .annual-fee {
  color: #FF5733; /* Set color for annual fee */
}
.credit-card-details p .cashback-percent {
  color: #8A2BE2; /* Set color for cashback percent to purple */
}

/* Add more classes and colors as needed */


.credit-card p .issuer {
  color: #4CAF50; /* Set color for issuer */
}

.credit-card p .network {
  color: #FFC107; /* Set color for network */
}

